body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.p-card {
  margin: 20px;
}

.p-card-image {
  border-radius: 10px;
  width: 300px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.p-card-info {
  margin-top: 10px;
  min-height: 100px;
}

.p-card-title {
  font-size: 24px;
  margin: 0px;
}

.p-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.succes-msg {
  color: #57a773;
  padding: 10px 15px;
}

.err-msg {
  color: #ee6352;
  padding: 10px 15px;
}

.vr46 {
  color: #ffdd00;
  font-size: 30px;
  font-weight: bold;
}

.vr46:hover {
  color: #ffdd00;
}

.aboutCard {
  width: 60%;
  height: 250px;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  margin-top: 50px;
  box-shadow: 0px 0px 13px 1px #616161;
}

.textArea {
  margin: 20px;
  margin-bottom: 10px;
  flex-grow: 1;
  overflow: hidden;
}

.aboutCard h3 {
  font-weight: bold;
}

.imageArea {
  width: 100%;
  flex-grow: 1;
  align-content: right;
  position: relative;
  bottom: 0;
}

img.aboutImages {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 0px 20px 20px 0px;
}

#titlesize{
    font-size: 120px;
    font-weight: bold
}

@media (max-width: 640px) {
  #titlesize {
    font-size: 60px;
  }

  #subTitlesize {
    font-size: 40px;
  }

  .aboutCard {
    flex-direction: column;
    height: 480px;
  }

  img.aboutImages {
  height: 100%;
  width: 100%;
  border-radius: 0px 0px 20px 20px;
  }
  
  .textArea {
  margin: 20px;
  flex-grow: 1;
  overflow: hidden;
  min-height: 120px;
}

}
